.bg-full-block {

	img {
		display: block;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}
.main-title {
	font-size: 52px;
	&_black-theme {
		color: rgba(var(--color-foreground));
	}
}
.article-main {

	letter-spacing: 0.06rem;
	h2,h3,h4 {
	  font-size: 40px;
	  &:not(:last-child) {
		margin-bottom: 1.8rem;
	  }
	}
	p {
	line-height: calc(1 + 0.8 / var(--font-body-scale));
	  &:not(:last-child) {
		margin-bottom: 1.2rem;
	  }
	}
	form {
		&:not(:last-child) {
			margin-bottom: 1.2rem;
		  }
	}
	@media (max-width: $md2 + px) {
		letter-spacing: 0.04rem;
		h2,h3,h4 {
			font-size: 30px;
			&:not(:last-child) {
			  margin-bottom: 1.2rem;
			}
		  }
		  p {
		  line-height: calc(1 + 0.8 / var(--font-body-scale));
			&:not(:last-child) {
			  margin-bottom: 0.8rem;
			}
		  }
	}
}
.main-button {
	display: inline-flex;
    justify-content: center;
    align-items: center;
    border: 0;
    padding: 0 3rem;
	line-height: 2.4;
	font-weight: 600;
    cursor: pointer;
    font-size: 1.5rem;
    text-decoration: none;

	border-radius: 5px;
	&:hover {
		opacity: 0.9;
	}
	&--white {
		color: black;
		background-color: rgb(var(--color-background));
	}
	&--black {
		color: rgb(var(--color-background));
		background-color: rgb(var(--color));
	}
}
.button-back {
	position: fixed;
	z-index: 2;
	bottom: 20px;
	right: 20px;
	font-size: 40px;
	background-color: rgb(var(--color));
	border-radius: 5px;
	width: 48px;
	text-align: center;
	line-height: 1.2;
	cursor: pointer;
	opacity: 0;
	transition: opacity 0.4s;
	&._show {
		opacity: 1;
	}
	&::before {
		color: white;

	}
}
.section-title {
	font-size: 37px;
	&:not(:last-child) {
	  margin-bottom: 1.8rem;
	}
}